body {
  font-family: lato, sans-serif;
}

img {
  height: 400px;
  width: 500px;
  margin-top: 20px;
}

select {
  border: 1px solid gray;
  border-radius: 5px;
  padding: 5px;
  font-size: 24px;
  font-weight: 700;
}

.loader {
  width: 40px;
  height: 98px;
  box-sizing: border-box;
  color: rgba(255, 61, 0, .9);
  background: #fff;
  border: 2px solid #000;
  border-radius: 20px 20px 4px 4px;
  margin: 300px auto;
  animation: fill 2s linear infinite alternate;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 15px;
  height: 35px;
  background: #000;
  border: 2px solid #000;
  border-bottom: none;
  animation: fillNeck 2s linear infinite alternate;
  position: absolute;
  top: 0%;
  left: 50%;
  transform: translate(-50%, -95%);
}

@keyframes fill {
  0% {
    box-shadow: inset 0 0;
  }

  50%, 100% {
    box-shadow: inset 0 -98px;
  }
}

@keyframes fillNeck {
  0%, 50% {
    box-shadow: inset 0 0;
  }

  100% {
    box-shadow: inset 0 -20px;
  }
}

